<template>
  <vs-row>
    <!-- Columna izquierda -->
    <vs-col vs-w="2" vs-sm="1" vs-lg="4">
    </vs-col>

    <!-- Columna central -->
    <vs-col vs-w="8" vs-sm="10" vs-lg="4">
      <div class="fecha">
        <date-picker
          v-model="fecha"
          value-type="format"
          format="DD/MM/YYYY"
          :disabled-date="notAfterToday"
          @change="setStartandEnd(fecha)"
        />
      </div>
      <vs-select
          id="zona"
          v-model="zona"
          class="mb-6"
          name="zona"
          width="100%" 
          @change="refresh()"
        >
        <vs-select-item value="peninsular" text="Pen√≠nsula" />
        <vs-select-item value="baleares" text="Baleares" />
        <vs-select-item value="canarias" text="Canarias" />
        <vs-select-item value="ceuta" text="Ceuta" />
        <vs-select-item value="melilla" text="Melilla" />
      </vs-select>      

      <!-- Precio actual -->
      <div class="header-block">
        <div class="hora-actual">üïò Hora actual: {{ hora_actual }}</div>
        <div class="precio-actual">
          {{ getIcon(active_price) }} Precio actual:
          {{ active_price | toKWh }} ‚ö°Ô∏è
        </div>
        <div class="min-precio">‚ôªÔ∏è M√≠nima diaria: {{ minima | toKWh }} ‚ö°Ô∏è</div>
        <div class="max-precio">
          ‚õîÔ∏è M√°xima diaria: {{ maxima | toKWh }} ‚ö°Ô∏è
        </div>
        <div class="media-precio">
          {{ getIcon(media_diaria) }} Media diaria:
          {{ media_diaria | toKWh }} ‚ö°Ô∏è
        </div>
      </div>
      <!-- Gr√°fico -->
      <Barra3 ref="grafico" />
      <!-- Lista de precios -->
      <div v-if="chart_datos">
        <div v-for="dato in chart_datos" :key="dato.value">
          <div :class="[isActiveHour(dato.datetime), pastHour(dato.datetime)]">
            {{ isMin(dato.value) ? "‚ôªÔ∏è" : "" }}
            {{ isMax(dato.value) ? "‚õîÔ∏è" : "" }}
            {{ getIcon(dato.value) }}
            <span class="hour-text">
              {{ dato.datetime | toHora }}
            </span>
            - {{ dato.value | toKWh }} ‚ö°Ô∏è
          </div>
        </div>
      </div>
      <div v-else>Obteniendo datos en tiempo real...</div>
    </vs-col>

    <!-- Columna derecha -->
    <vs-col vs-w="2" vs-sm="1" vs-lg="4">
    </vs-col>    
  </vs-row>
</template>

<script>
import axios from "axios";
import moment from "moment";
import Barra3 from "./Barra3.vue";

export default {
  name: "NewPrecioLuz",
  props: {
    msg: String,
  },
  components: {
    Barra3,
  },
  data() {
    return {
      url_api:
        "https://apidatos.ree.es/es/datos/mercados/precios-mercados-tiempo-real?time_trunc=hour&",
      url_dates: null,
      url_zona: null,
      chart_datos: null,
      active_price: 0,
      suma_total: 0,
      media_diaria: 0,
      minima: 100000,
      maxima: 0,
      hora_actual: null,
      junkievar: null,
      icono_rojo: "üî¥",
      icono_amarillo: "üü°",
      icono_verde: "üü¢",
      fecha: null,
      zona: "peninsular",
      zonas: ["peninsular", "canarias", "baleares", "ceuta", "melilla"],
      start_date: null,
      end_date: null,
    };
  },
  methods: {
    notAfterToday(date) {
      return date > new Date(new Date().setHours(0, 0, 0, 0));
    },    
    refresh() {
      // URLs
      this.$emit("toggleLoading");
      this.url_zona = "&geo_limit=" + this.zona;
      this.get_api_data();
    },
    getIcon(value) {
      if (value <= 150) return this.icono_verde;
      else if (value > 250) return this.icono_rojo;
      else return this.icono_amarillo;
    },
    pastHour(hour) {
      return moment(hour).format("HH") < moment().format("HH")
        ? "list-past"
        : "";
    },
    isActiveHour(hour) {
      return moment(hour).format("HH") === moment().format("HH")
        ? "list-active"
        : "list";
    },
    get_api_data() {
      axios
        .get(this.url_api + this.url_dates + this.url_zona)
        .then((response) => {
          this.chart_datos = response.data.included[0].attributes.values;
          this.calculateData();
          this.$emit("toggleLoading");
          this.$vs.notify({
            color: 'success',
            title: '<span class="material-icons">sync</span>',
            text: 'Datos recuperados satistactoriamente para zona ' + this.capitalize(this.zona),
            duration: 2000
          });
        })
        .catch(error => {
          this.$emit("toggleLoading");
          this.$vs.notify({
            color: 'danger',
            title: '<span class="material-icons">error</span>',
            text: 'Sucedi√≥ un error: ' + error.response.data.errors[0].detail,
            duration: 5000
          });          
        });
    },
    isMin(value) {
      return value === this.minima;
    },
    isMax(value) {
      return value === this.maxima;
    },
    calculateData() {
      var ahora = moment().format("HH");
      this.resetVars();

      Object.entries(this.chart_datos).map(([key, value]) => {
        this.junkievar = key;
        this.suma_total += value.value;
        this.$refs.grafico.datacollection.labels.push(
          moment(value.datetime).format("HH:mm")
        );
        this.$refs.grafico.datacollection.datasets[0].data.push(
          (value.value / 1000).toFixed(4)
        );

        if (value.value > this.maxima) this.maxima = value.value;
        if (value.value < this.minima) this.minima = value.value;

        if (moment(value.datetime).format("HH") === ahora) {
          this.active_price = value.value;
        }
      });

      this.$refs.grafico.renderChart(this.$refs.grafico.datacollection);
      this.media_diaria = this.suma_total / 24;
    },
    onlyPast() {
      var ahora = moment().format("HH");
      return this.chart_datos.filter(
        (franja) => moment(franja.datetime).format("HH") < ahora
      );
    },
    resetVars() {
      this.suma_total = 0;
      this.minima = 100000;
      this.maxima = 0;
      this.$refs.grafico.datacollection.labels = [];
      this.$refs.grafico.datacollection.datasets[0].data = [];
    },
    capitalize(texto) {
      return texto.charAt(0).toUpperCase() + texto.slice(1);
    },
    setStartandEnd(date) {
      this.start_date = moment(date, 'DD/MM/YYYY').startOf("day").format("YYYY-MM-DDTHH:mm:ss");
      this.end_date = moment(date, 'DD/MM/YYYY').endOf("day").format("YYYY-MM-DDTHH:mm:ss");
      this.url_dates = "start_date=" + this.start_date + "&end_date=" + this.end_date;
      this.refresh();
    }
  },
  mounted() {
    this.start_date = moment().startOf("day").format("YYYY-MM-DDTHH:mm:ss");
    this.end_date = moment().endOf("day").format("YYYY-MM-DDTHH:mm:ss");
    this.hora_actual = moment().format("HH:mm");

    // URLs
    this.url_dates = "start_date=" + this.start_date + "&end_date=" + this.end_date;
    this.url_zona = "&geo_limit=" + this.zona;

    this.fecha = moment().format("DD/MM/YYYY");
    this.get_api_data();
  },
  filters: {
    capitalize(texto) {
      return texto.charAt(0).toUpperCase() + texto.slice(1);
    },
    toKWh(mwh) {
      return (mwh / 1000).toFixed(4) + " ‚Ç¨ KW/h";
    },
    toHora(hora) {
      return moment(hora).format("HH:mm");
    },
  },
};
</script>