<template>
  <div id="app">
    <!-- Carga -->
    <loading :active.sync="isLoading" 
        :width="width"
        :opacity="opacity"
        :loader="style" 
        :color="colorLoader"
        :is-full-page="fullPage">
    </loading>

    <NewPrecioLuz msg="Precios de la Luz para hoy" @toggleLoading="toggleLoading" />
    <Footer copyright="Sugerencias/contacto" email="contacto@menudotimo.com" />
  </div>
</template>

<script>
import NewPrecioLuz from './components/NewPrecioLuz.vue';
import Footer from './components/Footer.vue';
// Import component
import Loading from 'vue-loading-overlay';
// Import stylesheet
import 'vue-loading-overlay/dist/vue-loading.css';

export default {
  name: 'App',
  components: {
    Loading,
    NewPrecioLuz,
    Footer
  },
  data() {
    return {
      width: 100,
      fullPage: true,
      style: 'dots',
      opacity: 0.9,
      colorLoader: '#3b82f6',
      isLoading: true,
    }
  },
  methods: {
    toggleLoading() {
      this.isLoading = !this.isLoading;
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
